





























































import Vue from "vue";

export default Vue.extend({
    data: () => ({
        work: [] as any,
    }),
    created() {
        const context = require
            .context("@/portfolio", true)
            .keys()
            .map((x) => x.substr(2, x.length));

        this.work = context;
    },
    components: {
        Menu: () => import("@/components/Menu.vue"),
        Footer: () => import("@/components/Footer.vue"),
    },
});
